
import Tab from 'vux/src/components/tab/tab.vue'
import TabItem from 'vux/src/components/tab/tab-item.vue'
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import Scroller from 'vux/src/components/scroller/index.vue'

import { mapState } from "vuex";
export default {
name: "topiclist",
props: ["name"],
components: {
Tab,
TabItem,
Group,
Cell,
Scroller
},
data: function() {
return {
lists: [],

page: 1,

limit: 15,

pullupConfig: {
content: "上拉加载更多",
downContent: "松开进行加载",
upContent: "上拉加载更多",
loadingContent: "加载中..."
},

pulldownConfig: {
content: "下拉刷新",
downContent: "松开进行刷新",
upContent: "下拉刷新",
loadingContent: "加载中……"
}
};
},
computed: {

...mapState(["topics"])
},
methods: {

getDescByTab(tab) {
return this.$store.getters.getTopicDescByTab(tab);
},

routeto(index) {








this.$router.push("/topiclist/" + this.topics[index].name);
},

freshData: function() {
var app = this;

this.$http
.get("/topics", {
params: {
tab: this.name,
page: 1,
limit: this.limit
}
})
.then(function(response) {
if (response.data && response.data.success) {
app.lists = response.data.data;
app.page = 1;

app.$refs.pullup.donePulldown();
}
});
},

loadMoreData() {
var app = this;
this.page = this.page + 1;

this.$http
.get("/topics", {
params: {
tab: this.name,
page: this.page,
limit: this.limit
}
})
.then(function(response) {
if (response.data.success) {



for (var i = 0; i < response.data.data.length; i++) {

app.lists.push(response.data.data[i]);

}


app.$refs.pullup.donePullup();
}
});
}
},




beforeRouteEnter: function(to, from, next) {





next(function(vm) {

vm.$http
.get("/topics", {
params: {

tab: to.params.name, //目的路由参数
page: vm.page,
limit: vm.limit
}
})
.then(function(response) {



if (response.data.success) {
vm.lists = response.data.data;
}
});
});
},


beforeRouteUpdate: function(to, from, next) {

var app = this;

this.$http
.get("/topics", {
params: {
tab: to.params.name,
page: this.page,
limit: this.limit
}
})
.then(function(response) {
if (response.data.success) {

app.lists = response.data.data;
}
});
}
};



