import Vue from 'vue';
import VueRouter from 'vue-router';
import Home from '@/components/Home.vue';
import TopicList from '@/components/TopicList.vue';
import Topic from '@/components/Topic.vue';
import Login from '@/components/Login.vue';
import Logout from '@/components/Logout.vue';
import Center from '@/components/Center.vue';
import Msg from '@/components/Msg.vue';
import User from '@/components/User.vue'
import Create from '@/components/Create.vue'
import Edit from '@/components/Edit.vue'

// 全局注册路由
Vue.use(VueRouter);

// 定义路由信息对象
const routes = [
    // { path: '/', name: 'home', component: Home },//默认打开Home组件
    {
        path: '/',
        redirect: '/topiclist/all'
    }, //输出重定向  重新定向到了topiclist文件中
    {
        path: '/topiclist/:name',
        name: 'topiclist', //:表示动态路由
        component: TopicList,
        props: true
    }, //props的值为布尔模式如果 props 被设置为 true，接受的路由参数作为props属性来传递
    {
        path: '/topic/:id',
        name: 'topic',
        component: Topic,
        props: true
    },
    // 登录操作
    {
        path: '/login',
        name: 'login',
        component: Login
    },
    //发表新话题
    {
        path: '/create',
        name: 'create',
        component: Create
    },
    // 个人中心
    {
        path: '/center',
        name: 'center',
        component: Center
    },
    {
        path: '/user/:loginname',
        name: 'user',
        component: User,
        props: true
    },
    // 消息通知
    {
        path: '/msg',
        name: 'msg',
        component: Msg
    },
    // 登出操作
    {
        path: '/logout',
        name: 'logout',
        component: Logout
    },
    //编辑主题
    {
        path: '/topic/:id/edit',
        name: 'edit',
        component: Edit,
        props: true
    }
];

// 创建路由管理器
const router = new VueRouter({
    routes //对象数组
});

// 对外输出模块
export default router;


